.CreateTodoButton {
    background-color: hwb(165 41% 18%);
    box-shadow: 0px 5px 15px rgb(176, 212, 222);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #fafafa;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 1;

    transform: rotate(0);
    transition: .3s ease;

}

.CreateTodoButton:hover{
    transform: rotate(224deg);
}